import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Menu } from "antd";

const items = [
  {
    label: "Home",
    key: "/",
  },
  {
    label: "Research",
    key: "SubMenu",
    children: [
      {
        label: "AI & Imaging Technologies",
        key: "/direction",
      },
      {
        label: "Scientific Applications",
        key: "/application",
      },
    ],
  },
  {
    label: "Publication",
    key: "/publication",
  },
  {
    label: "Team",
    key: "/team",
  },
  {
    label: "Teaching",
    key: "/teaching",
  },
  {
    label: "Contact",
    key: "/contact",
  },
];

const App = () => {
  const [current, setCurrent] = useState("mail");
  const navigate = useNavigate();

  const onClick = (e) => {
    setCurrent(e.key);
    navigate(e.key);
  };

  return (
    <div
      style={{
        padding: "10px 30px",
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between", // 
      }}
    >
        <div style={{ display: "flex", alignItems: "center" }}>
    <img
      src="/rawdata/logo_temp.png"
      alt="Logo"
      style={{
        height: "80px",
        marginRight: "10px",
        padding: "0px",
        marginTop: "-10px", //  margin  padding 
        marginLeft: "0px", //  padding
      }}
    />
  </div>

      {/*  Menu */}
      <Menu
        onClick={onClick}
        selectedKeys={[current]}
        mode="horizontal"
        items={items}
        style={{
          display: "inline-block", // 
          borderBottom: "none", // 
        }}
        className="text-xl"
      />
      <style>
        {`
          .ant-menu-horizontal > .ant-menu-item::after {
            border-bottom: 2px solid #1890ff; /*  */
            transform: scaleX(1); /*  */
          }
          .ant-menu-horizontal > .ant-menu-item {
            margin: 0 8px; /*  */
          }
        `}
      </style>
    </div>
  );
};

export default App;
