import React from "react";
import { List, Typography } from "antd";

const { Title, Text, Link } = Typography;

// Sample data for the publications
const publications = [
  {
    image: "./rawdata/publication_emdiffusion.png",
    title:
      "An Expectation-Maximization Algorithm for Training Clean Diffusion Models from Corrupted Observations",
    authors: "Weimin Bai, Yifei Wang, Wenzheng Chen, He Sun",
    link: {
      text: "NeurIPS 2024",
      url: "https://example.com/publication1",
    },
  },
  {
    image: "./images/p2.png",
    title:
      "Imaging biomacromolecules in action with liquid-phase electron microscopy",
    authors: "Jia-ye Li, He Sun, Huan Wang",
    link: {
      text: "Trends in Chemistry 2024",
      url: "https://example.com/publication2",
    },
  },
  {
    image: "./rawdata/publication3.png",
    title:
      "Discovering Structure From Corruption for Unsupervised Image Reconstruction",
    authors: "Oscar Leong, Angela F.Gao, He Sun, Katherine L.Bouman",
    link: {
      text: "IEEE Transactions on Computational Imaging 2023",
      url: "https://example.com/publication3",
    },
  },
  {
    image: "./rawdata/publication4.png",
    title: "Image Reconstruction without Explicit Priors",
    authors: "Oscar Leong, Angela F.Gao, He Sun, Katherine L.Bouman",
    link: {
      text: "ICASSP 2023",
      url: "https://example.com/publication4",
    },
  },
  {
    image: "./rawdata/publication5.jpg",
    title: "Deep learning-assisted analysis of single molecule dynamics from liquid-phaseelectron microscopy",
    authors: "Bin Cheng, Enze Ye, He Sun, Huan Wang",
    link: {
      text: "Chemical Communications 2024",
      url: "https://example.com/publication5",
    },
  },
  {
    image: "./rawdata/publication6.jpg",
    title: "Recovering a Molecule's 3D Dynamics from Liquid-phase Electron Microscopy",
    authors: "Enze Ye, Yuhang Wang, Hong Zhang, Yiqin Gao, Huan Wang, He Sun",
    link: {
      text: "ICCV 2023",
      url: "https://example.com/publication4",
    },
  },
];

const PublicationsList = () => {
  return (
    <div className="container mx-auto px-6 py-10">
      <List
        itemLayout="vertical"
        dataSource={publications}
        renderItem={(item) => (
          <List.Item>
            <div className="flex items-start space-x-4">
              {/* Image Section */}
              <img
                src={item.image}
                alt={item.title}
                className="w-72 object-cover rounded-md mr-5"
              />

              {/* Content Section */}
              <div>
                <Title level={3} className="mb-5 mt-2">
                  {item.title}
                </Title>
                <Text className="block text-gray-600 mb-6 text-base">{item.authors}</Text>
                {/* <Link href={item.link.url} target="_blank" className="text-blue-500 text-base">
                  {item.link.text}
                </Link> */}
              </div>
            </div>
          </List.Item>
        )}
      />
    </div>
  );
};

export default PublicationsList;
