import React from "react";
import { Card, Row, Col, Typography } from "antd";

const { Title, Text, Link } = Typography;

// 
const faculty = {
  name: "He Sun",
  image: "./teams/hesun.jpg",
  description:
    "He Sun is a Boya Young Fellow Assistant Professor at Peking University. His research primarily focuses on AI for scientific imaging, which tightly integrates optics, control and machine learning for scientific discoveries.",
  description1: "Prior to starting at Peking University, He was a postdoctoral researcher and Amazon AI4Science Fellow at Caltech CMS, where he mainly worked with Katie Bouman on black hole imaging. He received the Ph.D. from Princeton University in 2019 (advised by N. Jeremy Kasdin) and the bachelors degrees in Engineering Mechanics and Economics from Peking University in 2014.",
    email: "hesun@pku.edu.cn",
  links: [
    { label: "[Personal Website]", url: "http://users.cms.caltech.edu/~hesun/" },
    { label: "[Google Scholar]", url: "https://scholar.google.com/citations?user=c9V5HkYAAAAJ&hl=en&oi=ao" },
    { label: "[Github]", url: "https://github.com/HeSunPU" },
    { label: "[Twitter]", url: "https://twitter.com/_sun_he" },
  ],
};

const students = {
  phd: [
    { name: "Weimin Bai", image: "./teams/weimin.jpg" },
    { name: "Enze Ye", image: "./teams/enze.png" },
    { name: "Shaochi Ren", image: "./teams/shaochi.jpg" },
    { name: "Jinxin Liu", image: "./teams/jinxin.jpg" },
    { name: "Xiangyi Wang", image: "./teams/xiangyi.png" },
    { name: "Youjia Zheng", image: "./teams/youjia.png" },
    { name: "Hao Hu", image: "./teams/huhao.jpg" },
    { name: "Zhiru Liu", image: "./teams/zhiru.jpg" },
    { name: "Yiwei Ren", image: "./teams/yiwei.jpg" },

  ],
  master: [
    { name: "Zhiming Chang", image: "./teams/zhiming.jpg" },
    { name: "Yaqing Xu", image: "./teams/yaqing.jpg" },
    { name: "Yihang Zheng", image: "./teams/yihang.jpg" },
    { name: "Yuxuan Gu", image: "./teams/yuxuan.png" },
    { name: "Yubo Li", image: "./teams/yubo.jpg" },
    { name: "Qianhang wu", image: "./teams/qianhang.jpg" },
  ],
  undergraduate: [
    { name: "Siyi Chen", image: "./teams/siyi.png" },
    { name: "Weiheng Tang", image: "./teams/weiheng.png" },
    { name: "Yifei Wang", image: "./teams/yifei.png" },
  ],
  alumni: [
    { name: "Zhizhang Li", details: "Undergraduate@PKU -> PhD Student@WashU" },
    { name: "Lujing Xing", details: "Undergraduate@PKU -> PhD Student@UCLA" },
    { name: "Haiyue Li", details: "Undergraduate@PKU -> Master Student@ETH" },
  ],
};

const FacultyPage = () => {
  return (
    <div className="container mx-auto px-6 py-10">
      {/* Faculty Section */}
      <section className="mb-12">
        <Title level={2}>Principle Investigator</Title>
        <Row gutter={[32, 32]}>
          <Col xs={24} md={5}>
            <img
              src={faculty.image}
              alt={faculty.name}
              className="w-full h-auto rounded-lg shadow-lg"
            />
          </Col>
          <Col xs={24} md={18}>
            <Title className="mt-0 mb-4" level={2}>
              {faculty.name}
            </Title>
            <Text className="block mb-4 text-lg">{faculty.description}</Text>
            <Text className="block mb-4 text-lg">{faculty.description1}</Text>
            <Text className="block mt-8 mb-3 text-lg">
              Email:&nbsp;&nbsp;<Link className="text-lg" href={`mailto:${faculty.email}`}>{faculty.email}</Link>
            
            </Text>
            <div className="flex space-x-4 text-lg">
              {faculty.links.map((link, index) => (
                <Link className="text-lg" key={index} href={link.url} target="_blank">
                  {link.label}
                </Link>
              ))}
            </div>
          </Col>
        </Row>
      </section>

      {/* PhD Students Section */}
      <section className="mb-12">
        <Title level={3}>PhD Students</Title>
        <Row gutter={[16, 16]}>
          {students.phd.map((student, index) => (
            <Col key={index} xs={12} sm={8} md={4}>
              <Card
                hoverable
                cover={<img alt={student.name} src={student.image} />}
                className="text-center"
              >
                <Text className="font-bold">{student.name}</Text>
              </Card>
            </Col>
          ))}
        </Row>
      </section>

      {/* Master Students Section */}
      <section className="mb-12">
        <Title level={3}>Master Students</Title>
        <Row gutter={[16, 16]}>
          {students.master.map((student, index) => (
            <Col key={index} xs={12} sm={8} md={4}>
              <Card
                hoverable
                cover={<img alt={student.name} src={student.image} />}
                className="text-center"
              >
                <Text className="font-bold">{student.name}</Text>
              </Card>
            </Col>
          ))}
        </Row>
      </section>

      {/* Undergraduate Students Section */}
      <section className="mb-12">
        <Title level={3}>Undergraduate Students</Title>
        <Row gutter={[16, 16]}>
          {students.undergraduate.map((student, index) => (
            <Col key={index} xs={12} sm={8} md={4}>
              <Card
                hoverable
                cover={<img alt={student.name} src={student.image} />}
                className="text-center"
              >
                <Text className="font-bold">{student.name}</Text>
              </Card>
            </Col>
          ))}
        </Row>
      </section>

      {/* Alumni Section */}
      <section>
        <Title level={3}>Alumni</Title>
        <ul className="list-disc ml-6">
          {students.alumni.map((alumnus, index) => (
            <li key={index}>
              <Text>{alumnus.name}</Text>
              <Text className="text-gray-500"> - {alumnus.details}</Text>
            </li>
          ))}
        </ul>
      </section>
    </div>
  );
};

export default FacultyPage;
